.configuracoes{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    .conteudo{
        width: 100%;
        padding: 25px 0;
        @include desktop{
          width: 73.57%;
        }
        .configuracoes{
            border: 1px solid #dfdfdf;
            padding: 20px;
            form{
                width: 10%;
                max-width: 720px;
                margin: 0 auto;

                label{
                    display: inline-flex;
                    justify-content: center;
                    width: 150px;
                    height: 150px;
                    margin: 0 auto 40px auto;
                    display: block;
                    border-radius: 50%;

                    @include tablet{
                        width: 250px;
                        height: 250px;
                    }
                    margin-bottom:40px ;
                    input[type="file"]{
                        display:none;
                    }
                    img{
                        border-radius: 50%;
                        background-color: #009bdb;
                        width: 100%;
                        height: 100%;
                        background-size: cover;
                        background-position: center;
                        border: 1px solid #009bdb;

                        @include tablet {
                            max-width: 250px;
                            height: 250px;
                        }
                    }
                }
                input{
                    border:1px solid #dbdbdb;

                }

                 input[type="email"],
                 input[type="password"],
                 input[type="tel"]{
                    width: 100% ;
                    display: block;
                    margin-bottom: 30px;
                 }

                 .nome{
                    display: inline-flex;
                    width: 100%;
                    justify-content: space-between;
                    flex-wrap: wrap;
                    input[type="text"]{
                        width: 100%;
                        display: inline-block;
                        margin: 0px 0px 30px 0px;
                        @include desktop{
                            width: 46,80%;
                 }
                    }
            
                }
                input[type="submit"]{
                    font-size: 17px;
                    font-family: 'Montserrat Regular';
                    text-transform:none;
                    height: auto;
                    line-height: 17px;
                    padding: 7px 0;
                    margin: 0px auto;
                    display: block;
                    width: 100%;
                    max-width: 200px;


                }
            }

            
        }
       
    }



}