.pesquisa{
    display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	.conteudo{
        width: 100%;
        padding: 25px 0;
        @include desktop{
            width: 47.14%;  
        }
		.item{ 
			border: 1px solid #dfdfdf;
			padding: 20px 30px;
			margin-bottom: 35px;
			display: inline-flex;
			flex-wrap: wrap;
			align-items : flex-start; 

			.foto{
				width: 60px;
				margin-right: 15px;

				.img{
					width: 100%;
					background: #009bdb;
					border-radius: 50%;
					display: block;
				}
			}

			.info{
				width: calc(100% - 75px);
				

				p.nome{
					margin: 19px 0 11px 0;
					line-height: 1;
					font-size: 16px;
					color: #acacac;
					font-family: 'Montserrat Medium';

				}

			    .bio{
					font-size: 14px;
					color: #4b4b4b;
					font-family: 'Montserrat Regular';
				}
			}

		}

}
}