.lateral_esquerda{
    width: 100%;
    /*padding: 25px 0;*/
    @include desktop{
        width: 22,85%;
    }
    .topo{
        width: 100%;
        display: inline-flex;
        align-items: flex-start;
        margin-bottom: 25%;

        img{
            width: 60px;
            margin-right: 18px;
            border-radius: 50%;
            background-color: #009bdb;
            display: block;
        }
        .info{
            width:calc(100% - 78px);

            a{
                display: block;
                p{
                    font-size: 16px;
                    color: #acacac;
                    font-family:'Montserrat Medium';
                    line-height:1.8;
                }
            }
            .btn-seguir{
                margin-top: 4px;
                border: 0px;
                padding: 6px;
                $line_height: 1;
                font-size: 14px;
                color: #fff;
                background-color: #a0c49a;
                border-radius: 5px;
                cursor:pointer;
                transition: .25s;
                
                &:hover{
                    background-color: #748e6f;
                }
            }
        }
    }
    .links{
        margin-bottom: 25px;
        ul{
            li{
                border: 1px solid #dbdbdb;
                padding: 8px 9px;
                font-size: 16px;
                color: #b8b8b8;
                line-height: 1;
                margin-bottom: 15px;
                span{
                    float: right;
                    
                }
               &:nth-last-child(1){
                    margin-bottom: 0px;
               }
            }
        }
    }
    .form_quem_sou{
        width: 100%;
        form{
            width: 100%;
            position: relative;
            textarea{
                resize: nome;
                border: 1px solid #dbdbdb;
                width: 100%;
                padding: 10px 9px;
                font-size: 16px;
                color: #b8b8b8;
                font-family: 'Montserrat Regular';
                height: 150px;
                border-radius: 0 0 10px 0;
            }
            input[type="submit"]{
                margin-right: 0;
                display: bolck;
                margin-top: -42px;
                z-index: 0;
                position:absolute;
                right: 0;
            }
        }
    }
    .lista_mensagens{
        width: 100%;
        .item{ 
			border: 1px solid #dfdfdf;
			padding: 20px 15px;
			margin-bottom: 10px;
			display: inline-flex;
			flex-wrap: wrap;
			align-items : flex-start; 
            transition: .25s;
            cursor: pointer;
            &:hover{
                background-color: #f1f1f1;
            }

			.foto{
				width: 45px;
				margin-right: 15px;

				.img{
					width: 100%;
					background: #009bdb;
					border-radius: 50%;
					display: block;
				}
			}

			.info{
				width: calc(100% - 60px);

				p.nome{
					margin: 0px 0 5px 0;
					line-height: 1;
					font-size: 16px;
					color: #acacac;
					font-family: 'Montserrat Medium';

				}

			    .bio{
					font-size: 14px;
					color: #4b4b4b;
					font-family: 'Montserrat Regular';
				}
			}

		}
    }
}